.pagination {
    display: flex;
    justify-content: center;
    margin-top: 15px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  ul.pagination li {
    display: inline-block;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }
  
  ul.pagination li a {
    text-decoration: none;
    color: #796FB2;
    font-size: 1rem;
  }
  
  ul.pagination li.active a {
    color: white;
  }

  ul.pagination li.active {
    background-color: #796FB2;

  }
  
  ul.pagination li a:hover,
  ul.pagination li a.active {
    color: #574B9F;
  }
  
  .page-selection {
    width: 48px;
    height: 30px;
    color: #796FB2;
  }